<template>
  <div>
    <carft-gantte :datas="sourceData" />
  </div>
</template>

<script>
import carftGantte from '../components/carftGantte/carftGantte.vue'
import dayjs from 'dayjs'
export default {
  components: { carftGantte },
  data () {
    return {
      //
      sourceData: [
        {
          name: '001',
          gtArray: [
            {
              name: '001',
              status: 'passed',
              statusName: '',
              start: this.formatDT(dayjs().subtract(3, 'd').toString()),
              end: this.formatDT(dayjs().subtract(1, 'd').toString()),
            },
          ]
        },
        {
          name: '002',
          gtArray: [
            {
              name: '002',
              status: 'normal',
              statusName: '',
              start: this.formatDT(dayjs().subtract(1, 'd').toString()),
              end: this.formatDT(dayjs().add(4, 'd').toString())
            }
          ]
        },
        {
          name: '003',
          gtArray: [
            {
              name: '003',
              status: 'normal',
              statusName: '',
              start: this.formatDT(dayjs().subtract(1, 'd').subtract(8, 'h').toString()),
              end: this.formatDT(dayjs().add(2, 'd').toString()),
            }
          ]
        },
        {
          name: '004',
          gtArray: [
            {
              name: '004',
              status: 'future',
              statusName: '',
              start: this.formatDT(dayjs().add(8, 'h').toString()),
              end: this.formatDT(dayjs().add(1, 'week').toString()),
            }
          ]
        },
        {
          name: '005',
          gtArray: [
            {
              name: '005',
              status: 'future',
              statusName: '',
              start: this.formatDT(dayjs().add(2, 'd').toString()),
              end: this.formatDT(dayjs().add(2, 'week').toString()),
            }
          ]
        }
      ]
    }
  },
  computed: {},
  methods: {
    formatDT (dt) {
      return dayjs(dt).format('YYYY-MM-DD HH:mm:ss')
    }
  }
}

</script>
